<template>
  <div class="index">
    <Row type="flex" justify="center" class="code-row-bg" style="margin-bottom: 50px">
      <Carousel autoplay :autoplay-speed="speed" style="width: 1000px;margin: 5px;padding: 5px;">
        <CarouselItem>
          <img src="../../../static/index/wallls.com_121684.jpg" alt="">
        </CarouselItem>
        <CarouselItem>
          <img src="../../../static/index/04199_lonelyisland_1920x1080.jpg" alt="">
        </CarouselItem>
        <CarouselItem>
          <img src="../../../static/index/v2-73b8307b2db44c617f4e8515ce67dd39_r.jpg" alt="">
        </CarouselItem>
        <CarouselItem>
          <img src="../../../static/index/v2-899100d274cde24cea52da8b9c87e498_r.jpg" alt="">
        </CarouselItem>
      </Carousel>
    </Row>
    <Row type="flex" justify="center" align="middle">
      <Col style="font-weight:bolder;font-size: 50px">最新文章</Col>
    </Row>
    <Row v-for="item in textsList" :key="item.textId" >
      <Col offset="6">
        <Row type="flex" align="middle">
          <Col offset="1" style="font-size: 15px;">
            <Button @click="goUser(item)" type="text">
            <img class="round_icon" src="../../../static/img/default.jpg"
                 v-if="item.user.userHeadportrait==='default.jpg'"/>
              <Head-portrait :src="item.user.userHeadportrait" v-else></Head-portrait>
            </Button>
          </Col>
          <Col class="headBox">
            <p style="font-size:25px;">
              {{item.textTitle}}
            </p>
            <span  >
              <Tooltip content="作者" style="margin-right: 5px">
              <h5>{{item.user.userName}}</h5>
              </Tooltip>
            </span>
            <Tooltip content="最后修改日期" style="margin-right: 5px">
              {{item.textReleasedate}}
            </Tooltip>
            <span>点击量 {{item.textCount}}</span>
            <span>评论数 {{item.textCommentnum}}</span>
            <span>点赞数 {{item.textLikenum}}</span>
            <span>收藏数 {{item.textCollectnum}}</span>
          </Col>
        </Row>
        <Row>
          <Col offset="2">
          <p v-html="item.textContent.replace(/<(style|script|iframe)[^>]*?>[\s\S]+?<\/\1\s*>/gi, '').
          replace(/<[^>]+?>/g, '').replace(/\s+/g, ' ').replace(/ /g, ' ').replace(/>/g, ' ').substring(0,300)"
           style="width: 700px">
          </p>
          </Col>
          <Col offset="12" >
          <Button type="text" @click="goText(item)">查看更多内容</Button>
          </Col>
          <Col  style="border-bottom: 1px solid gray;width: 780px;margin-left: 50px">
          </Col>
        </Row>
      </Col>
    </Row>
    <Row type="flex" justify="center" align="middle">
      <Col style="font-weight:bolder;font-size: 25px">
    到底部了QAQ
      </Col>
    </Row>
  </div>
</template>

<script>
  import HeadPortrait from '../page/HeadPortrait'

  export default {
    data() {
      return {
        flag: false,
        speed: 5000,
        textsList: []
      }
    },
    methods: {
      getAllText() {
        this.axios.get('textAll').then(res => {
          if (res.data.code === 100) {
            this.textsList = res.data.map.texts
          } else {
            this.$Message.textsList("获取文章列表失败")
          }
        }).catch(error => {
          this.$Message.warning("获取文章列失败")
        })
      },goText(item){
        this.$router.push('/text?textId='+item.textId)
      },goUser(item){
        this.$router.push('/personHome/'+item.textWriterid)
      }
    }, mounted() {
      this.getAllText()
    },components:{HeadPortrait}
  }
</script>

<style scoped>
  img {
    width: 1000px;
    height: auto;
  }

  .round_icon:hover {
    transform: scale(1.4);
  }

  .round_icon {
    width: 50px;
    height: 50px;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    transition: all 0.6s;
  }

  .headBox {
    margin: 10px;
    padding: 10px;
  }
</style>



